<template>
  <div class="app">
    <a-form :form="form" @submit="handleSubmit">
      <a-form-item class="form-item-title" label="">
        <a-input
          v-decorator="[
          'news_title',
          {rules: [{ required: true, message: '!' }], initialValue:news_title,}
        ]"
          placeholder=""
        />
      </a-form-item>
      <!-- <a-form-item class="form-item-title" label="" :wrapper-col="{ span: 12 }">
        <a-select
          v-decorator="[
          'news_type',
          {rules: [{ required: true, message: '!' }],initialValue:news_type}
        ]"
          placeholder=""
        >
          <a-select-option value="male"></a-select-option>
          <a-select-option value="female"></a-select-option>
        </a-select>
      </a-form-item>-->
      <a-form-item class="form-item-title" label="" v-if="residences.length>0">
        <a-cascader
          @change="handleChange"
          v-decorator="[
          'news_type',
          {
            initialValue:news_type,
            rules: [{ type: 'array', required: true, message: '!' }],
          }
        ]"
          placeholder=""
          :options="residences"
        />
      </a-form-item>
      <a-form-item>
        <mceeditor v-model="news_content" @on-upload-complete="uploadcomplete"></mceeditor>
        <!-- Value:{{Value}} -->
      </a-form-item>
      <a-form-item :wrapper-col="{ span: 12, offset: 5 }">
        <a-button type="primary" html-type="submit"></a-button>
      </a-form-item>
    </a-form>
    <!--<mce-editor 
    :config           = "Config"
     v-model          = "Value"
    :url              = "Url" //
    :max-size         = "MaxSize"//
    :accept           = "Accept"//
    :with-credentials = false
    @on-ready         = "onEditorReady"//
    @on-destroy       = "onEditorDestroy"
    @on-upload-success= "onEditorUploadComplete"
    @on-upload-fail   = "onEditorUploadFail"
    on-upload-complete
    ></mce-editor>-->
  </div>
</template>

<script>
import { insertnews, getnewitem } from "@/api/news";
import { mapGetters } from "vuex";
import mceeditor from "@/components/Tinymce";
window.tinymce.baseURL = "/static/tinymce";
window.tinymce.suffix = ".min";

export default {
  components: {
    mceeditor
  },
  computed: {
    ...mapGetters(["name"])
  },
  data() {
    return {
      news_content: "",
      news_title: "",
      news_type: [],
      id: "",
      formLayout: "horizontal",
      form: this.$form.createForm(this),
      residences: []
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      let { params } = this.$route.query;
      if (params) {
        params = JSON.parse(params);
        console.log("", params);
        this.news_content = params.news_content;
        this.news_title = params.news_title;
        this.news_type = params.news_type.split("/");
        this.id = params.id;
      }
      getnewitem().then(res => {
        let { data } = res;
        if (data.code == 200) {
          for (let item of data.data) {
            this.residences.push({
              value: item.columnlevel,
              label: item.columnlevel
            });
          }
        }
      });
    },
    uploadcomplete(params) {
      console.log("", params);
    },
    handleChange(value, list) {
      list = list || this.residences;
      // console.log('!@!@',list)
      value = value[0];
      let _index = 0;
      let isHaveChild = false;
      // console.log('',value)
      list.forEach((item, index) => {
        if (item.value == value) {
          _index = index;
        }
        if (item.hasOwnProperty("children")) {
          isHaveChild = true;
        }
      });
      if (!_index && isHaveChild) {
        this.handleChange(value, list[_index].children);
      } else {
        getnewitem({ type: value }).then(res => {
          let { data } = res;
          if (data.code == 200) {
            list[_index].children = [];
            for (let item of data.data) {
              list[_index].children.push({
                value: item.columnlevel,
                label: item.columnlevel
              });
            }
          }
        });
      }
    },
    handleSubmit(e) {
      console.log("mc", this.name);
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          let news_type = values.news_type.join("/");
          insertnews({
            news_title: values.news_title,
            news_type,
            news_content: this.news_content,
            create_user: this.name,
            id: this.id
          }).then(res => {
            this.$message.success("");
            this.$router.push("/newslist");
          });
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.app {
  width: 1204px;
  margin: 0 auto;
  overflow: hidden;
  .form-item-title {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }
}
</style>


